import React from 'react';
import {
  withStyles,
  Typography,
  Box,
  Paper,
  Grid,
  Card,
  CardContent,
  CardActions,
  Collapse,
  TableContainer,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  IconButton
} from '@material-ui/core';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import classnames from 'classnames';


const styles = {
  card: {
    width: '100%',
  },
  media: {
    display: 'flex',
    justifyContent: 'flex-end'
  },
  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto'
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: 'red',
  },
}

class ResultCard extends React.Component {
  state = { expanded: false };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  makeRow(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  };

  createData() {
    const data = [
      this.makeRow('Frozen yoghurt', 159, 6.0, 24, 4.0),
      this.makeRow('Ice cream sandwich', 237, 9.0, 37, 4.3),
      this.makeRow('Eclair', 262, 16.0, 24, 6.0),
      this.makeRow('Cupcake', 305, 3.7, 67, 4.3),
      this.makeRow('Gingerbread', 356, 16.0, 49, 3.9),
    ];
    return data;
  }

  render() {
    const { classes, fitCategory, score, description, name, link, statsFilePath } = this.props;
    const rows = this.createData();

    return (

      <Card className={classes.card} >
        <CardContent>
          <Grid container spacing={3}>
            <Grid item xs={8}>
              <Box>
                <Typography variant="h4" align="left">
                  {`${fitCategory} (${score}%)`}
                </Typography>
                <br />
                <br />
                <Typography paragraph align="left">
                  {description}
                </Typography>
              </Box>
            </Grid>
            <Grid item xs={4}>
              <Box align="center">
                <Typography variant="h4">
                  {name}
                </Typography>
                <img src={link} alt="logo" className='icon' style={{ 'height': '8em', 'margin': '2em' }} />
              </Box>
            </Grid>
          </Grid>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
          <IconButton
            className={classnames(classes.expand, {
              [classes.expandOpen]: this.state.expanded,
            })}
            onClick={this.handleExpandClick}
            aria-expanded={this.state.expanded}
            aria-label="Show more"
          >
            <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent>

            <TableContainer component={Paper}>
              <Table className={classes.table} aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell>Dessert (100g serving)</TableCell>
                    <TableCell align="right">Calories</TableCell>
                    <TableCell align="right">Fat&nbsp;(g)</TableCell>
                    <TableCell align="right">Carbs&nbsp;(g)</TableCell>
                    <TableCell align="right">Protein&nbsp;(g)</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {rows.map((row) => (
                    <TableRow key={row.name}>
                      <TableCell component="th" scope="row">
                        {row.name}
                      </TableCell>
                      <TableCell align="right">{row.calories}</TableCell>
                      <TableCell align="right">{row.fat}</TableCell>
                      <TableCell align="right">{row.carbs}</TableCell>
                      <TableCell align="right">{row.protein}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </CardContent>
        </Collapse>
      </Card>
    );
  }
}

export default withStyles(styles)(ResultCard);
